import preact from 'preact';

import Home from './components/home';
import Landing from './components/landing';

import "./index.css";

class Base extends preact.Component{
    constructor(){
        super();
        this.state = {
            stateName : '',
        }
    }

    componentDidUpdate(){
        console.log('updating the main state');
    }

    updateStateName(stateName){
        localStorage.clear();
        localStorage.setItem('stateName',stateName);

        // if stateName is 'Kerala' => language is malayalam else hindi
        if(stateName === 'കേരളം')
            localStorage.setItem('lang', 'ml_IN');
        else
            localStorage.setItem('lang', 'hi_IN');

        this.setState({stateName: stateName});
    }

    render(){
        let stateName = localStorage.getItem('stateName');

        if(stateName){
            return <Home/>;
        }
        else{
            return <Landing updateStateName={ (stateName)=> this.updateStateName(stateName) }/>;
        }
    }
}

export default Base;


